@import '../../../CommonFiles/commonStyles.module';

.timeLineWrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  .time {
    display: flex;
    align-items: center;
    height: fit-content;
  }

  .timeSlider {
    display: flex;
    justify-content: center;
    width: 280px;
    margin: -17px -10px 0 -10px;


    & > * {
      &:hover {
        cursor: pointer;
      }
      width: 250px;
      color: $mainTextColor;
    }
  }

}